<div class="p-5 text-center"><div class="inline-block"><h1 class="text-xl font-bold mx-5">Login</h1><form class="bg-gray-200 rounded m-5 inline-block" @submit.prevent="submitPassword(pwd)"><label class="m-2" for="password">Password:</label><input class="m-2 rounded p-1" id="password" :class="pwdInvalid ? 'border-red-500 border-2' : ''" type="password" autocomplete="password" v-model="pwd"><button class="m-2 rounded bg-green-300 px-2 py-1" type="submit">Submit</button></form><p class="text-red-500 font-semibold" v-if="pwdInvalid">Password Invalid!</p></div><p class="text-center text-red-500" v-if="serverProblem"> There was a problem with the server. If the problem persists, please contact an admimistartor.</p></div>