<div class="cursor-copy p-0" :class="{'bg-green-200': lightUp}" @dragenter.prevent.stop @dragover.prevent.stop @dragenter="lightUp = true" @dragleave="lightUp = false" @drop.prevent="handleDrop($event)"><label class="block min-h-full min-w-full text-center" :for="id">{{values.length}} file(s) uploaded</label><input class="hidden" :id="id" :multiple="multiple" type="file" :accept="accept" @change="handleChange($event)"></div>