<div class="p-4"><font-awesome-icon class="block mx-auto h-40 animate-spin" v-if="loadState === 'waiting'" icon="spinner" size="3x"></font-awesome-icon><p class="text-center" v-else-if="loadState === 'failed'">Records could not be loaded</p><mike-record-table v-show="loadState === 'done'" v-model:records="records" id="table"></mike-record-table><div class="h-20"></div><div class="fixed bottom-0 left-0 h-20 w-screen flex flex-row justify-between items-center p-5 border-t-2 bg-white" v-if="loadState === 'done'"><button class="rounded bg-green-300 py-1 px-3" @click="addAndScrollToBottom()"><font-awesome-icon class="mr-2" icon="plus"></font-awesome-icon>Add Record</button><span class="text-red-400" v-if="submissionState === 'failed'">There was a problem submitting your changes. Please contact an administrator if the problem persists.</span><span class="text-green-500" v-if="submissionState === 'done'">The database was successfully updated!</span><button class="rounded bg-green-300 py-1 px-3 " @click="saveChanges()"><font-awesome-icon class="mr-2" v-if="submissionState !== 'waiting'" icon="save"></font-awesome-icon><font-awesome-icon class="mr-2 animate-spin" v-else="submissionState === 'waiting'" icon="spinner"></font-awesome-icon>{{submissionState !== "waiting" ? "Save changes" : "Saving changes..."}}</button></div></div>