<div class="p-5 text-center grid grid-cols-2"><div class="border-r"><h1 class="text-xl font-bold mx-5">Update with a CSV</h1><form class="bg-gray-200 rounded my-5 w-1/2 h-40 center mx-auto flex flex-col" @submit.prevent="submitUploadedFile()"><file-drop class="block center m-2 mb-0 rounded-md bg-white flex-auto" v-model:files="files" accept=".csv"></file-drop><div class="flex-initial m-0 p-0"><button class="block m-2 rounded bg-green-300 px-2 py-1 " type="submit">Submit</button></div></form><p v-if="fileSubmissionState === 'waiting'"><font-awesome-icon class="animate-spin mr-1" icon="spinner"></font-awesome-icon></p><p v-else-if="fileSubmissionState === 'done'">The application database was successfully updated!</p><p v-else-if="fileSubmissionState === 'failed'">The update failed because of internal server problems. If the problem persists, contact an administrator.</p></div><div><h1 class="text-xl font-bold mx-5">Update from the MIKE database</h1><form class="bg-gray-200 rounded m-5 inline-block" @submit.prevent="triggerDbUpdate()"><button class="m-2 rounded bg-green-300 px-2 py-1">Start automatic update</button></form><p v-if="mikeUpdateState === 'waiting'"><font-awesome-icon class="animate-spin" icon="spinner"></font-awesome-icon></p><p v-else-if="mikeUpdateState === 'done'">The application database was successfully updated!</p><p v-else-if="mikeUpdateState === 'failed'">The update failed because of internal server problems. If the problem persists, contact an administrator.</p></div></div>